body {
  margin: 0;
  font-family: 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

main {
  padding: 0 15px;
  margin: 0 auto;
  max-width: 1230px; }

.hljs {
  border-radius: 3px; }

.hljs-inline pre {
  font-size: 12px; }

.hljs-inline pre,
.hljs-inline .hljs {
  display: inline !important;
  padding: 2px; }

.filtr-item {
  z-index: 1; }
  .filtr-item .colored {
    border-radius: 3px;
    color: white;
    display: block;
    font-size: 36px;
    font-weight: bold;
    width: 100%;
    text-align: center;
    padding: 20px 0; }
    .filtr-item .colored.green {
      background-color: #3cd27b; }
    .filtr-item .colored.orange {
      background-color: #f39c12; }
    .filtr-item .colored.purple {
      background-color: #800080; }

.Navigation {
  border-radius: 0 !important;
  margin-bottom: 2em !important; }
  .Navigation__navbar {
    margin: 0 auto !important;
    max-width: 1200px; }
  .Navigation__logo {
    width: 120px;
    height: 32px; }

.Footer {
  color: rgba(0, 0, 0, 0.5);
  padding: 22px 0;
  box-shadow: 0 -3px 8px rgba(0, 0, 0, 0.1);
  text-align: center; }

.DelayModeTutorialPage .mode-alternate,
.DelayModeTutorialPage .mode-progressive {
  background-color: #272721;
  border-radius: 3px;
  margin-top: 10px;
  padding: 10px;
  position: relative;
  z-index: 0; }
  .DelayModeTutorialPage .mode-alternate .filtr-item,
  .DelayModeTutorialPage .mode-progressive .filtr-item {
