    padding: 10px;
    height: 84px;
    width: 16.66%; }

.FilteringTutorialPage .toggle-filter,
.FilteringTutorialPage .active-filter {
  background-color: #272721;
  border-radius: 3px;
  margin-top: 10px;
  padding: 10px;
