    width: 16.66%; }

.LayoutsTutorialPage .horizontal-layout,
.LayoutsTutorialPage .vertical-layout,
.LayoutsTutorialPage .packed-layout,
.LayoutsTutorialPage .same-height-layout,
.LayoutsTutorialPage .same-width-layout,
.LayoutsTutorialPage .same-size-layout {
  background-color: #272721;
  border-radius: 3px;
  margin-top: 10px;
  padding: 10px;
  position: relative;
  z-index: 0; }
  .LayoutsTutorialPage .horizontal-layout .filtr-item,
  .LayoutsTutorialPage .vertical-layout .filtr-item,
  .LayoutsTutorialPage .packed-layout .filtr-item,
  .LayoutsTutorialPage .same-height-layout .filtr-item,
  .LayoutsTutorialPage .same-width-layout .filtr-item,
  .LayoutsTutorialPage .same-size-layout .filtr-item {
    display: flex;
    padding: 10px;
    height: 84px;
    width: 16.66%; }

.LayoutsTutorialPage .same-width-layout .filtr-item:nth-child(2),
.LayoutsTutorialPage .same-width-layout .filtr-item:nth-child(6),
.LayoutsTutorialPage .same-width-layout .filtr-item:nth-child(8),
.LayoutsTutorialPage .same-width-layout .filtr-item:nth-child(12) {
  height: 168px; }

.LayoutsTutorialPage .same-width-layout .filtr-item:nth-child(3),
.LayoutsTutorialPage .same-width-layout .filtr-item:nth-child(5),
.LayoutsTutorialPage .same-width-layout .filtr-item:nth-child(7),
.LayoutsTutorialPage .same-width-layout .filtr-item:nth-child(10) {
  height: 252px; }

.LayoutsTutorialPage .same-height-layout .filtr-item:nth-child(1),
.LayoutsTutorialPage .same-height-layout .filtr-item:nth-child(3) {
  width: 33.32%; }

.LayoutsTutorialPage .same-height-layout .filtr-item:nth-child(7) {
  width: 66.48%; }

.LayoutsTutorialPage .same-height-layout .filtr-item:nth-child(9) {
  width: 49.86%; }

.LayoutsTutorialPage .same-height-layout .filtr-item:nth-child(12) {
  width: 100%; }

.LayoutsTutorialPage .packed-layout .filtr-item {
  width: 16.638%; }

.LayoutsTutorialPage .packed-layout .filtr-item:nth-child(2),
.LayoutsTutorialPage .packed-layout .filtr-item:nth-child(6) {
  width: 33.276%; }

.LayoutsTutorialPage .packed-layout .filtr-item:nth-child(7),
.LayoutsTutorialPage .packed-layout .filtr-item:nth-child(9),
.LayoutsTutorialPage .packed-layout .filtr-item:nth-child(12) {
  width: 49.914%; }

.LayoutsTutorialPage .packed-layout .filtr-item:nth-child(4),
.LayoutsTutorialPage .packed-layout .filtr-item:nth-child(8),
.LayoutsTutorialPage .packed-layout .filtr-item:nth-child(10),
.LayoutsTutorialPage .packed-layout .filtr-item:nth-child(11) {
  height: 168px; }

.LayoutsTutorialPage .horizontal-layout {
  overflow-y: scroll; }

.LayoutsTutorialPage .vertical-layout {
  overflow-x: scroll;
  max-height: 300px; }


.ShowcasePage {
  overflow: hidden; }
  .ShowcasePage .Filterizr__filter-controls,
  .ShowcasePage .Filterizr__secondary-controls,
  .ShowcasePage .Filterizr__shuffle-controls,
  .ShowcasePage .Filterizr__search-control {
    display: flex;
    align-content: space-between;
    align-items: center;
    margin: 0 auto;
    max-width: 900px;
    padding: 0 30px 0 20px; }
  .ShowcasePage .Filterizr__secondary-controls {
    justify-content: space-between; }
  .ShowcasePage .Filterizr__sort-controls {
    display: flex; }
    .ShowcasePage .Filterizr__sort-controls .form {
      margin-left: 15px; }
  .ShowcasePage .Filterizr__secondary-controls,
  .ShowcasePage .Filterizr__search-control {
    margin-top: 15px; }
  .ShowcasePage .filtr-container {
    margin: 0 auto;
    max-width: 900px;
    -webkit-transition: height 1000ms ease-out;
    transition: height 1000ms ease-out;
    overflow: hidden; }
    .ShowcasePage .filtr-container .filtr-item {
      display: inline-block;
      width: 50%;
      padding: 20px;
      position: relative; }
      @media only screen and (min-width: 992px) {
        .ShowcasePage .filtr-container .filtr-item {
          width: 33%; } }
      .ShowcasePage .filtr-container .filtr-item .item-index,
      .ShowcasePage .filtr-container .filtr-item .item-desc {
        position: absolute;
        color: white;
        text-transform: uppercase;
        padding: 5px 10px;
        background-color: rgba(0, 0, 0, 0.5);
        border-radius: 3px;
        z-index: 1; }
      .ShowcasePage .filtr-container .filtr-item .item-index {
        top: 25px;
        left: 25px; }
      .ShowcasePage .filtr-container .filtr-item .item-desc {
        bottom: 25px;
        left: 25px; }
  .ShowcasePage__playground-wrapper {
    display: flex;
    margin-bottom: 13px;
    height: 280px; }
    .ShowcasePage__playground-wrapper > div {
      width: 100%; }
      .ShowcasePage__playground-wrapper > div .jsoneditor-tree-inner {
        padding-top: 10px;
        padding-bottom: 50px !important; }

.SortingTutorialPage .Filterizr__sort-controls {
  display: flex; }
  .SortingTutorialPage .Filterizr__sort-controls .two.buttons {
    max-width: 190px; }
  .SortingTutorialPage .Filterizr__sort-controls .form {
    margin-left: 15px; }

.SortingTutorialPage .sortable-grid {
  background-color: #272721;
  border-radius: 3px;
  margin-top: 10px;
  padding: 10px;
  position: relative;
  z-index: 0; }
  .SortingTutorialPage .sortable-grid .filtr-item {
    display: flex;
    padding: 10px;
    height: 84px;
    width: 16.66%; }
    .SortingTutorialPage .sortable-grid .filtr-item .black-letter {
      color: black; }
    .SortingTutorialPage .sortable-grid .filtr-item .blue-letter {
      color: #2c6ccc; }

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

main {
  flex-grow: 1;
  width: 100%; }
  @media only screen and (max-width: 991px) {
    main {
      max-width: 100%;
      overflow-x: hidden; } }

