body {
  margin: 0;
  font-family: 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

main {
  padding: 0 15px;
  margin: 0 auto;
  max-width: 1230px; }

.hljs {
  border-radius: 3px; }

.hljs-inline pre {
  font-size: 12px; }

.hljs-inline pre,
.hljs-inline .hljs {
  display: inline !important;
  padding: 2px; }

.filtr-item {
  z-index: 1; }
  .filtr-item .colored {
    border-radius: 3px;
    color: white;
    display: block;
    font-size: 36px;
    font-weight: bold;
    width: 100%;
    text-align: center;
    padding: 20px 0; }
    .filtr-item .colored.green {
      background-color: #3cd27b; }
    .filtr-item .colored.orange {
      background-color: #f39c12; }
    .filtr-item .colored.purple {
      background-color: #800080; }
